.marketplace-header {
  -webkit-box-pack: justify;
  -webkit-box-align: center;
  align-content: space-between;
  align-items: center;
  color: var(--spice-text);
  display: flex;
  justify-content: space-between;
  margin: 16px 0;
}

.marketplace-sort-bar {
  align-items: center;
  display: flex;
}

.marketplace-sort-container {
  position: relative;
  display: flex;
}

.marketplace-tabBar-headerItem {
  -webkit-app-region: no-drag;
  display: inline-block;
  pointer-events: auto;
}

.marketplace-tabBar-headerItemLink {
  margin: 0 8px 0 0;
}

.marketplace-tabBar-active {
  background-color: var(--spice-tab-active);
  border-radius: 4px;
}

.marketplace-tabBar-headerItemLink {
  border-radius: 4px;
  color: var(--spice-text);
  display: inline-block;
  margin: 0 8px;
  padding: 8px 16px;
  position: relative;
  text-decoration: none !important;
  cursor: pointer;
}

.marketplace-tabBar-nav {
  -webkit-app-region: drag;
  pointer-events: none;
  width: 100%;
}

.marketplace-tabBar-headerItem .optionsMenu-dropBox {
  color: var(--spice-text);
  border: 0;
  max-width: 150px;
  height: 42px;
  padding: 0 30px 0 12px;
  background-color: initial;
  cursor: pointer;
  appearance: none;
}

.marketplace-tabBar-headerItem .optionsMenu-dropBox svg {
  position: absolute;
  margin-left: 8px;
}

div.marketplace-tabBar-headerItemLink {
  padding: 0;
}

/* Card preview image error placeholder */
