  content: '';
  display: block;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;

  background-color: var(--spice-subtext);
  filter: brightness(50%);

  /* https://developer.mozilla.org/en-US/docs/Web/CSS/clip-path */
  /* https://icons8.com/icons/set/box */
