  clip-rule: nonzero;
  transform: scale(0.5);
}

.main-cardImage-imageWrapper--error {
  box-shadow: none;
}

.main-card-card:hover .main-cardImage-imageWrapper--error::before {
  filter: brightness(100%);
}

.marketplace-cardSubHeader,
.marketplace-card__bottom-meta {
  margin-top: 4px;
  white-space: normal;
  color: var(--spice-subtext);
}

.marketplace-card__bottom-meta {
  margin-top: 10px;
}

.marketplace-longDescription {
  display: flex;
}

.marketplace-card--installed {
  flex: 0 !important;
  -webkit-box-flex: 0 !important;
  height:350px
}
.marketplace-card-desc{
  /*
  TODO: Make this look nice.
  */
    font: -webkit-small-control;
  height: 45px;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}

.searchbar--bar__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-grow: 1;
}
.searchbar-bar{
  border: unset;
  background-color: var(--spice-sidebar) !important;
  border-radius: 5px !important;
  padding-left: 10px;
  padding-right: 10px;
  color: var(--spice-text) !important;
  z-index: 1000;
}

// The dropdown menus (sort by)
.marketplace-sort-container .optionsMenu-dropBox {
  grid-gap: 8px;
  align-items: center;
  background-color: transparent;
  border-radius: 4px;
  display: grid;
  grid-template-columns: 1fr 16px;
  color: rgba(var(--spice-rgb-text), .7);
  border: 0;
  height: 32px;
  margin-left: 8px;
  padding: 0 8px 0 12px;
}
.marketplace-sort-container .optionsMenu-dropBox:hover {
  color: var(--spice-text);
}
#marketplace-config-container input {
  width: 100%;
  margin-top: 10px;
  padding: 0 5px;
  height: 32px;
  border: 0;
  color: var(--spice-text);
  background-color: initial;
  border-bottom: 1px solid var(--spice-text);
}

option {
  background-color: var(--spice-button);
}
